<template>
  <div class="editormd">
    <input class="inputBox" placeholder="" maxlength='60' v-model="title"/>
    <input class="inputBox" placeholder="()" v-model="category"/>
    <mavon-editor v-model='content' ref=md @imgAdd="$imgAdd" @change="$change" @imgDel="$imgDel"/>
    <button class="submitButton" @click="submit"></button>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'editormd',
  data () {
    return {
      content: '', // 
      imgs: {}, // 
      articleId: undefined, // 
      isChange: false, // 
      title: undefined,
      date: undefined,
      category: undefined
    }
  },
  created () {
    var info = this.$route.query.info
    if (info !== undefined) {
      this.articleId = info[0]
      this.title = info[1]
      this.date = info[2]
    }
    this.content = this.$route.query.content
  },
  methods: {
    $imgAdd (pos, $file) {
      // 
      // fileminiurlbase64
      this.imgs[pos] = $file.miniurl
    },
    $change (value, render) {
      // 
      this.isChange = true
    },
    $imgDel (pos) {
      // base64
      delete this.imgs[pos[0]]
    },
    upLoadImg () {
      if (this.articleId === undefined) {
        let date = new Date()
        // 
        let currentYear = new Date()
        currentYear.setMonth(0)
        currentYear.setDate(1)
        // 86400000 = 24 * 60 * 60 * 1000
        let hasTimestamp = date - currentYear
        let hasDays = Math.ceil(hasTimestamp / 86400000) + 1 // 
        let m = date.getMonth() + 1
        let d = date.getDate()
        let h = date.getHours()
        let mt = date.getMinutes()
        let y = date.getFullYear()
        m = m > 9 ? m : '0' + m
        d = d > 9 ? d : '0' + d
        h = h > 9 ? h : '0' + h
        mt = mt > 9 ? mt : '0' + mt
        // 
        this.date = y + '' + m + '' + d + ''
        // ,**
        let k = (hasDays * 24 + h) * 60 + mt
        // iduserid+(2020)+()
        k = ('000000' + k).slice(-6) //  60
        k = ('000' + (y - 2020)).slice(-3) + k //  30
        let id = '000' + this.$user.userId
        id = id.slice(-3) // userId
        // 12articleId
        this.articleId = id + k
      }
      var formData = this.imgs
      // id
      formData['articleId'] = this.articleId
      formData['userId'] = this.$user.userId
      axios({
        url: this.$baseUrl + 'imgToUrl',
        data: formData,
        method: 'post'
      }).then(resp => {
        // 
        let errorImg = {}
        // ()
        this.imgs = {}
        // url
        let baseUrl = this.$baseUrl
        for (var i in resp.data) {
          if (resp.data[i] === 'no') errorImg[i] = 'no'
          this.$refs.md.$img2Url(i, baseUrl + resp.data[i])
        }
        let arr = Object.keys(errorImg)
        if (arr.length > 0) {
          alert('')
          return false
        }
        // url(nextTick)
        this.$refs.md.$nextTick(() => {
          // 
          this.upArticle()
        })
      })
        .catch(error => {
          alert(' ' + error)
        })
    },
    upArticle () {
      axios({
        url: this.$baseUrl + 'upLoad',
        data: {
          'content': this.content,
          'articleId': this.articleId,
          'title': this.title !== undefined ? this.title : this.date + '-',
          'date': this.date,
          'category': this.category !== undefined ? this.category : '',
          'userId': this.$user.userId
        },
        method: 'post'
      }).then(resp => {
        // 
        if (resp.data === 'N') alert('')
        else {
          alert('')
          // 
          this.isChange = false
        }
      })
        .catch(error => {
          alert(' ' + error)
        })
    },
    submit () {
      if (!this.isChange) {
        alert('')
        return false// 
      }
      // upImgupArticle
      this.upLoadImg()
    }
  }
}
</script>
<style>
  .editormd{
        width:100%;
        margin-top: 1%;
        opacity: 0.9;
    }
  .submitButton{
    margin-top: 10px;
    padding: 5px;
    border-radius: 10px;
    background: rgb(43, 42, 42);
    color: white;
    border:1px solid rgb(48, 150, 184);
    transition: all .3s ease 0s;
  }
  .submitButton:hover{
    padding: 10px;
    background: rgb(48, 150, 184);
    cursor: pointer;
  }
  .inputBox{
  background:whitesmoke;
  border: 0px;
  opacity: 0.8;
  border-radius: 5px;
  position: relative;
  top: 50%;
  height: 30px;
  padding-left: 5px;
 }
</style>



